*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  font-family: Lato;
}

* {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
}

body {
  min-height: 800px;
  box-sizing: border-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: inherit;
  position: relative;
}

.header {
  flex-direction: column;
  display: flex;
}

.search {
  height: 48px;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  display: flex;
}

button {
  z-index: 99;
  height: 25px;
  width: 25px;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  outline: none;
  transition: all .2s ease-in-out;
}

.location-btn {
  margin-right: -40px;
}

.star-btn {
  margin-left: -50px;
}

input.location {
  color: #000;
  width: calc(100% - 430px);
  background-color: #fff;
  border: 1px solid #7c7cb0;
  border-radius: 30px;
  outline: none;
  padding: .8em 3.6em;
  font-family: inherit;
  font-size: 18px;
}

.city-search {
  border-radius: 20px;
  outline: none;
  font-size: 16px;
}

.slider {
  cursor: pointer;
  max-width: calc(100% - 200px);
  flex-direction: row;
  justify-content: flex-start;
  gap: 10px;
  display: flex;
  overflow: hidden;
}

.carousel {
  flex-direction: row;
  justify-content: center;
  gap: 8px;
  margin-top: 40px;
  display: flex;
}

.slide {
  height: 28px;
  background-color: rgba(16, 33, 54, .5);
  border-radius: 20px;
  align-items: center;
  gap: 5px;
  padding: 5px 10px;
  display: flex;
}

.city-close-btn {
  width: 15px;
  height: 15px;
  color: rgba(255, 255, 255, .5);
  border: none;
  justify-content: center;
  align-items: center;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  display: flex;
}

.city-close-btn:hover {
  color: #fff;
}

.city-name {
  letter-spacing: 0;
  text-align: left;
  color: #fff;
  font-family: Lato;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
}

.next, .prev {
  background: rgba(255, 255, 255, .2);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

